import logo from './logo.svg';
import './App.css';
import './components/greet';
import Greet from './components/greet';
import ListEmployeeComponent from './components/ListEmployeeComponent';
import FooterComponent from './components/FooterComponent';
import HeaderComponent from './components/HeaderComponent';
import {BrowserRouter as Router,Route,Switch }from 'react-router-dom';
import CreateEmployeeComponent from './components/CreateEmployeeComponent'
import ViewEmployeeComponent from './components/ViewEmployeeComponent'
import 'react-app-polyfill/stable';


function App() {
  return (
    <div>
      <Router>
            <div class="container">
          <HeaderComponent/>
              <div className="container">
                <Switch>
                  <Route path="/" exact component={ListEmployeeComponent}></Route>
                  <Route path="/employees" component={ListEmployeeComponent}></Route>
                  <Route path="/addEmployee/:id" component={CreateEmployeeComponent}></Route>
                  <Route path="/updateEmployee/:id" component={CreateEmployeeComponent}></Route>
                  <Route path="/deleteEmployee/:id" component={ListEmployeeComponent}></Route>
                  <Route path="/viewEmployee/:id" component={ViewEmployeeComponent}></Route>
                              
                </Switch>
                
              </div>    
        <FooterComponent/>      
          </div>
        </Router>
    </div>
    
  );
}

export default App;
