import axios from "axios";

const EMPLOYEE_API_BASE_URL="/api/v1/employees";

const EMPLOYEE_API_Add_URL="/api/v1/addEmployee";

const EMPLOYEE_API_Update_URL="/api/v1/updateEmployee";

const EMPLOYEE_API_Delete_URL="/api/v1/deleteEmployee";

class EmployeeService{

    getEmployee(){
        return axios.get(EMPLOYEE_API_BASE_URL);
    }

    addEmployee(employee){
        return axios.post(EMPLOYEE_API_Add_URL,employee);
    }

    findById(employeeId){
        return axios.get(EMPLOYEE_API_BASE_URL +"/"+ employeeId )
    }

    updateEmployee(employee,id,lastName){
        return axios.put(EMPLOYEE_API_Update_URL+ "/"+id,employee)
    }
    deleteEmployee(id){
        return axios.delete(EMPLOYEE_API_Delete_URL+"/"+id)
    }

}


export default new EmployeeService()