import React, { Component } from 'react'
import EmployeeService from '../components/service/EmployeeService';
import { Provider as AlertProvider } from 'react-alert'
import ViewEmployeeComponent from './ViewEmployeeComponent';

class ListEmployeeComponent extends Component {
    constructor(props) {
        super(props)

        this.state = {
                employees: []
        }

        this.addEmployee=this.addEmployee.bind(this);
      
    }

    
    componentDidMount(){
        EmployeeService.getEmployee().then((res)=>{

            this.setState({employees:res.data} )
        });
        
    }

    addEmployee(){
        this.props.history.push('/addEmployee/-1')
    }

    editEmployee(id){
        this.props.history.push(`/addEmployee/${id}`);
    }

    deleteEmployee(id){
        alert('You are about to delete a record!!')
        EmployeeService.deleteEmployee(id).then( res => {
            this.setState({employees: this.state.employees.filter(employee => employee.id !== id)});


        });
    }

    viewEmployee(id){
        this.props.history.push(`/viewEmployee/${id}`);
        
      
    }

    render() {
        return (
            <div>
                

                 <h2 className="text-center">Employees List</h2>
                 <div className="row">
                     <button className='btn  btn-primary' onClick={this.addEmployee}>Add Employee</button>
                 </div>
                 <div className = "row">
    
                 </div>
                 <br></br>
                 <div className = "row">
                        <table className = "table table-striped table-bordered">

                            
                                <tr>
                                    <th> Employee First Name</th>
                                    <th> Employee Last Name</th>
                                    <th> Employee Email Id</th>
                                    <th> Actions</th>
                                </tr>
                            
                            <tbody>
                                {
                                    this.state.employees.map(
                                        employee => 
                                        
                                        <tr key = {employee.id}>
                                             <td> { employee.firstName} </td>   
                                             <td> {employee.lastName}</td>
                                             <td> {employee.email}</td>
                                            <td>
                                            <button onClick={ () => this.editEmployee(employee.id)} className="btn btn-info">Update </button>  
                                            <button style={{marginLeft: "10px"}} onClick={ () => this.deleteEmployee(employee.id)} className="btn btn-danger">Delete </button>
                                            <button style={{marginLeft: "10px"}} onClick={ () => this.viewEmployee(employee.id)} className="btn btn-primary">View </button>

                                            </td>
                                            
                                        </tr>
                                    )
                                }
                            </tbody>
                        </table>

                 </div>

            </div>
        )
    }
}

export default ListEmployeeComponent